<template>
  <div class="doc-container">
    <div class="doc-container-main">
      <div class="params-content" v-if="state.appKey">
        <h2>对接参数：</h2>
        <ul class="params-item">
          <li>
            <span>APPKEY: {{ state.appKey }}</span>
            <span style="margin-left: 50px"
              >APPSCERT: {{ state.appSecret }}</span
            >
          </li>
        </ul>
      </div>
      <div v-html="html"></div>
    </div>
  </div>
</template>

<script setup lang="ts">
import { onMounted, reactive, ref } from "vue";

import { marked } from "marked";
import { scrollToTop } from "jz-tool-lib";

import { getKeySecretApi } from "@/api/interface-doc";

const text = ref(`## 接口格式说明

### 1、请求鉴权

云中台对外提供的接口（http和https），采用统一方式鉴权，均在请求头部增加相关参数：

| 参数名称  | 参数类型 | 说明                                                         | 备注                            |
| :-------- | :------- | :----------------------------------------------------------- | :------------------------------ |
| xwsse     | String   | appkey和appsecret和timestamp组合后的字符串MD5值。例如appkey1234567890appsecret1234567890timestamp1668996363；MD5加密后C792B0337D0CB50D4237CFCB1240B282 | 注：组合的字符串顺序必须一致    |
| timestamp | String   | 服务器当前系统时间戳；毫秒值                                 | 注：这里时间戳与xwsse时间戳一致 |
| appkey    | String   | 平台分配的appkey                                             |                                 |

| 注意：Content-Type:application/json;charset=UTF-8 |
| :------------------------------------------------ |

### 2、统一返回类型

表：2.2 统一返回类型

| 参数名称   | 参数类型 | 说明                                | 备注 |
| :--------- | :------- | :---------------------------------- | :--- |
| statusCode | int      | 返回code=0表示成功，否则失败        |      |
| msg        | String   | 当statusCode!=0，返回具体的错误消息 |      |
| data       | Object   | 当statusCode=0时，返回具体的数据    |      |`);
const html = ref(``);
html.value = marked(text.value);

const state = reactive({
  appKey: "",
  appSecret: "",
});

const getKeySecret = async () => {
  const { data } = await getKeySecretApi.getData();
  state.appKey = data.appKey;
  state.appSecret = data.appSecret;
};

onMounted(() => {
  scrollToTop(document.querySelector(".doc-container"));
  getKeySecret();
});
</script>
